export const guid = () => {
	function s4() {
		return Math.floor((1 + Math.random()) * 0x10000)
			.toString(16)
			.substring(1);
	}

	return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
};

export const normalizeNavCategoryToRoute = (subCategory) => {
	switch (subCategory) {
		case ' ':
			return 'schoolFolders';
		case ' ':
			return 'officeFolders';
		case '':
			return 'copyBooks';
		case '':
			return 'notebooks';
		case '':
			return 'stickers';
		case '':
			return 'dictionaries';
		case ' ':
			return 'writingSupplies';
		case ' ':
			return 'schoolSupplies';
		case ' ':
			return 'officeSupplies';
		case '':
			return 'zno';

		case '':
			return 'casket';
		case ' ':
			return 'decorativeBoxes';
		case '':
			return 'figures';
	}
};

export const objectToArrayKeys = (obj) => {
	if (!obj)
		return;
	return Object.keys(obj).map(i => i);
};

export const getRandomArbitrary = (min, max) => Math.random() * (max - min) + min;

